module.exports = "import React, {Component} from 'react';\nimport {Typography} from '@material-ui/core';\nimport {FuseChipSelect} from '@fuse';\n\nconst suggestions = [\n    'Sea',\n    'Sky',\n    'Forest',\n    'Aerial',\n    'Art'\n].map(item => ({\n    value: item,\n    label: item\n}));\n\nclass SimpleExample extends Component {\n    state = {\n        tags: [\n            {\n                value: \"nature\",\n                label: \"Nature\"\n            },\n            {\n                value: \"city\",\n                label: \"City\"\n            },\n            {\n                value: \"landscape\",\n                label: \"Landscape\"\n            }\n        ]\n    };\n\n    onSubmit = (model) => {\n        console.info('submit', model);\n    };\n\n    handleChipChange = (value) => {\n        this.setState({tags: value});\n    };\n\n    render()\n    {\n        const {tags} = this.state;\n\n        return (\n            <div className=\"w-full max-w-sm pt-64 pb-224\">\n\n                <Typography className=\"text-24 mt-24 mb-8\" component=\"h2\">Standart</Typography>\n\n                <FuseChipSelect\n                    className=\"w-full my-16\"\n                    value={tags}\n                    onChange={this.handleChipChange}\n                    placeholder=\"Select multiple tags\"\n                    textFieldProps={{\n                        label          : 'Tags',\n                        InputLabelProps: {\n                            shrink: true\n                        },\n                        variant        : 'standard'\n                    }}\n                    options={suggestions}\n                    isMulti\n                />\n\n                <Typography className=\"text-24 mt-24 mb-8\" component=\"h2\">Outlined</Typography>\n\n                <FuseChipSelect\n                    className=\"w-full my-16\"\n                    value={tags}\n                    onChange={this.handleChipChange}\n                    placeholder=\"Select multiple tags\"\n                    textFieldProps={{\n                        label          : 'Tags',\n                        InputLabelProps: {\n                            shrink: true\n                        },\n                        variant        : 'outlined'\n                    }}\n                    options={suggestions}\n                    isMulti\n                />\n\n                <Typography className=\"text-24 mt-24 mb-8\" component=\"h2\">Filled</Typography>\n\n                <FuseChipSelect\n                    className=\"w-full my-16\"\n                    value={tags}\n                    onChange={this.handleChipChange}\n                    placeholder=\"Select multiple tags\"\n                    textFieldProps={{\n                        label          : 'Tags',\n                        InputLabelProps: {\n                            shrink: true\n                        },\n                        variant        : 'filled'\n                    }}\n                    options={suggestions}\n                    isMulti\n                />\n            </div>\n        );\n    }\n}\n\nexport default SimpleExample;\n"